"use client"

import {
  Users,
  Building2,
  Package,
  ShieldCheck,
  Wrench,
  Headphones,
} from "lucide-react"
import { cn } from "@/lib/utils"

const categories = [
  { id: "hr", icon: Users, label: "HR Services" },
  { id: "office", icon: Building2, label: "Office Support" },
  { id: "assets", icon: Package, label: "Asset Management" },
  { id: "access", icon: ShieldCheck, label: "Access Control" },
  { id: "infra", icon: Wrench, label: "Infrastructure" },
  { id: "support", icon: Headphones, label: "IT Support" },
]

interface CategorySidebarProps {
  selectedCategory: string
  onSelectCategory: (id: string) => void
}

export function CategorySidebar({ selectedCategory, onSelectCategory }: CategorySidebarProps) {
  return (
    <aside className="w-full h-full bg-white border-r border-gray-100 p-6">
      <h2 className="text-xs font-semibold text-muted-foreground uppercase tracking-wide mb-4">
        Categories
      </h2>
      <nav className="space-y-1">
        {categories.map((category) => {
          const isActive = selectedCategory === category.id
          return (
            <button
              key={category.id}
              type="button"
              onClick={() => onSelectCategory(category.id)}
              className={cn(
                "w-full flex items-center gap-3 px-4 py-3 rounded-full text-sm font-medium transition-all duration-200",
                isActive
                  ? "bg-primary text-primary-foreground shadow-lg shadow-indigo-500/20"
                  : "text-gray-500 hover:bg-gray-50 hover:text-foreground"
              )}
            >
              <category.icon className="w-5 h-5" />
              <span>{category.label}</span>
            </button>
          )
        })}
      </nav>
    </aside>
  )
}
