"use client"

import React from "react"

import { useState } from "react"
import { Clock, Zap, CheckCircle2, Settings, FileText } from "lucide-react"
import { cn } from "@/lib/utils"

interface Service {
  id: string
  title: string
  code: string
  description: string
  icon: React.ElementType
  status: "active" | "draft"
  sla: string
  avgTime: string
  successRate: string
}

interface ServiceCardProps {
  service: Service
}

export function ServiceCard({ service }: ServiceCardProps) {
  const [isHovered, setIsHovered] = useState(false)
  const Icon = service.icon

  return (
    <div
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
      className="group relative bg-white border border-gray-100 rounded-2xl shadow-sm hover:shadow-lg transition-all duration-300 overflow-hidden"
    >
      <div className="p-6">
        {/* Header */}
        <div className="flex items-start justify-between mb-4">
          <div className="w-12 h-12 rounded-xl bg-indigo-50 flex items-center justify-center text-primary">
            <Icon className="w-6 h-6" />
          </div>
          <span
            className={cn(
              "px-3 py-1 rounded-full text-xs font-medium",
              service.status === "active"
                ? "bg-emerald-50 text-emerald-600"
                : "bg-gray-100 text-gray-500"
            )}
          >
            {service.status === "active" ? "Active" : "Draft"}
          </span>
        </div>

        {/* Body */}
        <div className="mb-4">
          <h3 className="text-base font-semibold text-foreground mb-1 group-hover:text-primary transition-colors">
            {service.title}
          </h3>
          <p className="text-xs font-mono text-muted-foreground mb-2">
            {service.code}
          </p>
          <p className="text-sm text-muted-foreground line-clamp-2">
            {service.description}
          </p>
        </div>

        {/* Footer with Metrics */}
        <div
          className={cn(
            "pt-4 border-t border-gray-100 transition-all duration-300",
            isHovered ? "opacity-0 h-0 pt-0 border-t-0 overflow-hidden" : "opacity-100"
          )}
        >
          <div className="flex items-center justify-between text-xs text-muted-foreground">
            <div className="flex items-center gap-1.5">
              <Clock className="w-3.5 h-3.5" />
              <span>SLA: {service.sla}</span>
            </div>
            <div className="flex items-center gap-1.5">
              <Zap className="w-3.5 h-3.5" />
              <span>{service.avgTime}</span>
            </div>
            <div className="flex items-center gap-1.5">
              <CheckCircle2 className="w-3.5 h-3.5 text-emerald-500" />
              <span>{service.successRate}</span>
            </div>
          </div>
        </div>

        {/* Hover Actions */}
        <div
          className={cn(
            "flex items-center gap-3 transition-all duration-300",
            isHovered ? "opacity-100 pt-4 border-t border-gray-100" : "opacity-0 h-0 overflow-hidden"
          )}
        >
          <button
            type="button"
            className="flex-1 flex items-center justify-center gap-2 px-4 py-2.5 rounded-xl bg-gradient-to-r from-primary to-violet-500 text-white text-sm font-medium shadow-lg shadow-indigo-500/20 hover:shadow-xl transition-shadow"
          >
            <Settings className="w-4 h-4" />
            Configure Prompt
          </button>
          <button
            type="button"
            className="flex items-center justify-center gap-2 px-4 py-2.5 rounded-xl bg-gray-50 text-gray-600 text-sm font-medium hover:bg-gray-100 transition-colors"
          >
            <FileText className="w-4 h-4" />
            Logs
          </button>
        </div>
      </div>
    </div>
  )
}
